.logo {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
}

.logo__image {
  margin-right: 16px;
  width: 55px;
  height: 55px;
  grid-row: 1 / -1;
  grid-column: 1 / 2;

  @include vp-767 {
    width: 35px;
    height: 35px;
  }
}

.logo__company-name {
  margin: 2px 0 0;
  padding: 0;

  color: $color-default-white;
  font: 700 25px/120% $ff-roboto-condensed;

  @include vp-1023 {
    margin: 5px 0 0;
  }

  @include vp-767 {
    display: none;
  }
}

.logo__slogan {
  margin: -6px 0 0;
  padding: 0;

  color: transparentize($color-default-white, 0.3);
  font: 500 12px/120% $ff-roboto;

  @include vp-767 {
    display: none;
  }
}
